@media screen and (max-width: 1024px) {
    .generalBox{
        width: 100%;

    }
    .chatBox{
        width:100%;
    }
}
@media screen and (max-width: 768px) {
    .generalBox{
        width: 100%;
        display: inline-block;
        background: #FFFFFF;
    }
    .userListBox{
        width: 100%;
    }
    .chatBox{
        width: 100%;
        height: 95%;
        display: block;
        border-radius: unset;
    }
    .chatBox__UserList{
        width: 100%;
    }
    .generalBox__media__header{
        width: 100%;
        height: 5%;
        display: flex;
        align-items: center;
        background-color: #5682a3;
    }
    .generalBox__media{
        width: 100%;
        height: 100%;
    }
    .generalBox__media__header__backImg{
        margin-left: 10px;
    }
}
